import { Component, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http';
import { AddMedicineService } from './../services/AddMedicineService';
import { MeasurementAndDosageSizesService } from './../services/MeasurementAndDosageSizesService';
import { DosageAndSizeValuesService } from './../services/DosageAndSizeValuesService';
import { AddDosageService } from './../services/AdddosageService';
import { categoryModel } from './../models/categoryModel';
import { subcategoryModel } from './../models/subcategoryModel';
import { MeasurementModel } from './../models/MeasurementModel';
import { DosageModel } from './../models/DosageModel';
import { GetDosageModel } from './../models/GetDosageModel';
import { MedicineModel } from './../models/MedicineModel';
import { PricePerQuantityModel } from './../models/PricePerQuantityModel';
import { DosageSizesAndValueModel } from './../models/DosageSizesAndValueModel';

@Component({
  selector: 'DosageSize',
  providers: [DosageAndSizeValuesService],
  styles: [require('./DosageAndSizeValues.style.scss')],
  template: require('./DosageAndSizeValues.template.html'),
  encapsulation: ViewEncapsulation.None,
  host: {
    class: 'dosage-page app'
  },
})
export class DosageSizeComponent {
  router: Router;
  public DosageValue: any;
  public DosageId: any;
  public GetDosageModel: GetDosageModel[] = [];
  public SingleDosageModel: any[] = [];
  public UserType: any;

  constructor(public _addDosageAndSizeValuesService: DosageAndSizeValuesService, router: Router) {
    this.UserType = localStorage.getItem("UserType");
    if (this.UserType == null) {
      this.router.navigate(["/app/login"]);

    }
    else {

      this.router = router;
      this._addDosageAndSizeValuesService.getDosageValue().subscribe(
        response => {
          console.log(response);
          this.GetDosageModel = response.data;

          for (var i = 0; i < response.data.length; i++) {

            var dosageObject = {
              Id: response.data[i].DosageAndSizes.Id,
              UnitName: response.data[i].DosageAndSizes.UnitName
            };
            this.SingleDosageModel.push(dosageObject);

          }


        });
    }
  }
  getDosageId(DosageId) {
    this.DosageId = DosageId;
  }
  AddDosagevalue() {
    this._addDosageAndSizeValuesService.AddDosageValue(this.DosageId, this.DosageValue).subscribe(
      response => {
        console.log(response);
        jQuery("#snackbar1").html(response.message);
        this.myFunction();
      });
    this.DosageValue = undefined;
  }

  myFunction() {
    // Get the snackbar DIV
    var x = document.getElementById("snackbar1")

    // Add the "show" class to DIV
    x.className = "show";

    // After 3 seconds, remove the show class from DIV
    setTimeout(function () { x.className = x.className.replace("show", ""); }, 3000);
  }


}




