import { Injectable, Inject } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';
import { ServiceUrl } from './ServiceUrl';
@Injectable()
export class DosageAndSizeValuesService {
    urlService = new ServiceUrl();
    constructor(private http: Http) {

    }
    AddDosageValue(DosageId,DosageValue)
    {
        let body = JSON.stringify({ DosageAndSizesId: DosageId, Value:DosageValue});
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'post', headers: headers });

        return this.http.post(this.urlService.baseUrl + "Chemist/addUnitValue", body, options)
            .map(res => res.json());

    }
    getDosageValue()
    {
        let body = JSON.stringify({});
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ method: 'get', headers: headers });

        return this.http.get(this.urlService.baseUrl + "Chemist/getAllUnitValues", options)
            .map(res => res.json());

    }
 

   
}


